@import "../../global.scss";

.projectdescription {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    @include mobile {
        flex-direction: column;
        align-items: center;
    }

    .left {
        flex: 0.65;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 75vh;
        overflow: hidden;

        @include mobile {
            flex: 0.2;
        }

        .imgcontainer {
            display: flex;
            width: 100%;
            height: 100%;
            align-items: center;
            overflow: hidden;
        }

        .imgcontainer img {
            width: 100%;
            height: auto;

            @include mobile {
                height: 100%;
                width: auto;
            }
        }

        .vidcontainer {
            width: 100%;
            height: 100%;
        }
    }

    .right {
        flex: 0.35;
        display: flex;
        align-items: center;

        @include mobile {
            flex: 0.80;
        }

        .wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 10px;

            @include mobile {
                height: 100%;
                padding: 0;
            }

            h2 {
                color: black;
                font-size: 1.5em;

                @include mobile {
                    font-size: 1em;
                    font-weight: 700;
                }

            }

            p {
                color: black;
                font-size: 1em;
                text-align: left;
            }

            a {
                text-decoration: none;
                color: $mainColor;
                font-weight: 700;
            }
        }

    }

}

.arrow {
    position: absolute;
    color: black;
    bottom: 5px;
    animation: arrowBlink 5s linear infinite;
}

@keyframes arrowBlink {
    50% {
        opacity: 0;
    }
}